.container {
  box-sizing: border-box;
  background-color: #f5f5f5;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.container__watch {
  position: relative;
}

@keyframes rotate-clock {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.watch {
  border: 20px dotted #000;
  border-radius: 50%;
  justify-content: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.watch__minute {
  position: absolute;
}

.watch__second {
  position: absolute;
  top: 25%;
}

.watch__center {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.minute {
  transform-origin: bottom;
  background-color: green;
  border-radius: 10px;
  width: 2%;
  height: 50%;
  animation: 60s linear infinite rotate-clock;
}

.second {
  transform-origin: bottom;
  background-color: #00f;
  border-radius: 10px;
  width: 4%;
  height: 25%;
  animation: 5s linear infinite rotate-clock;
}

.center {
  background-color: orange;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

